import React, { useState } from 'react';
import styled, { keyframes } from 'styled-components';
import { motion } from 'framer-motion';

import { LogoComponent, HomeButton, SocialIcons, ParticleComponent } from "../subComponents"
import { Link } from 'react-router-dom';
import { Hlogo } from './AllSVGs';
import Intro from './Intro';
import Music from '../subComponents/Music';


const MainContainer = styled.div`
background: ${props => props.theme.body};
width: 100vw;
height: 100vh;
overflow: hidden;

position: relative;
h2,h3,h4,h5,h6{
  font-family: "Karla", sans-serif;
  font-weight: 500;
}
`

const Container = styled.div`
padding: 2rem;
`
const Contact = styled(Link)`
color: ${props => props.theme.text};
font-size: 1.6rem;
position: fixed;
top: 2rem;
right: 1rem;
z-index: 1;
`

const Skills = styled(Link)`
color: ${props => props.click ? props.theme.body : props.theme.text};
position: fixed;
top: 50%;
left: 2.7rem;
transform: translate(-50%,-50%) rotate(-90deg);
z-index: 1;
`
const Work = styled(Link)`

position: fixed;
top: 80%;
left: 46vw;

background-color: #444141;
border-radius: 4px;
color: #fff;
cursor: pointer;
padding: 15px 30px;
font-size: 18px;
font-weight: bold;
letter-spacing: 1px;
border: none;

 :hover {
  background-image: linear-gradient(90deg, #0046D6 0%, #05CCF1  50%, #05E2FF 75%, #0046D6 100%);
  animation: slidernbw 5s linear infinite;
  color: #000;
 }

 @keyframes slidernbw {
  to {
   background-position: 20vw;
  }
}

z-index: 1;

@media screen and (max-width: 950px){
  left: 42vw;
}

@media screen and (max-width: 610px){
  left: 34vw;
}
`

const BottomBar = styled.div`
position: absolute;
bottom: 1rem;
right: 0;
width: 100%;

display: flex;
justify-content: space-evenly;
`
const Project = styled(Link)`
color: ${props => props.click ? props.theme.body : props.theme.text};
z-index: 1;
`

const About = styled(Link)`
color: ${props => props.theme.text};
z-index: 1;
`

const rotate = keyframes`
from{
  transform: rotate(0);
}
to{
  transform: rotate(360deg);
}
`

const Center = styled.button`
position: absolute;
top: ${props => props.click ? "88%" : "50%" };
left: ${props => props.click ? "92%" : "50%" };
transform: translate(-50%,-50%);
border: none;
outline: none;
background-color: transparent;
cursor: pointer;

display: flex;
flex-direction: column;
justify-content: center;
align-items: center;

>:first-child{
  animation: ${rotate} infinite 5.5s linear;
}

>:last-child{
  padding-top: 1rem;
}
`

const DarkDiv = styled.div`
position: absolute;
top: 0;
bottom: 0;
right: 50%;
background-color: #000;
width: ${props => props.click ? "50%" : "0%" };
height: ${props => props.click ? "100%" : "0%" };
z-index: 1;
transition: height 0.5s ease, width 1s ease 0.5s;
`

const Main = () => {

  const [click, setClick] = useState(false);

  const handleClick = () => setClick(!click);

  return (
    <div>
      <MainContainer>
      <DarkDiv click={click}/>
        <Container>
          <HomeButton />
          <LogoComponent theme={click ? "dark" : "light"}/>
          <SocialIcons theme={click ? "dark" : "light" }/>
          <ParticleComponent theme={"light"}/>
          <Music />

          <Center click={click}>
            <Hlogo onClick={()=> handleClick()} width={click ? 100 : 250} height={click ? 100 : 250} fill={"black"} />
            <spam>{click ? "" : "click"}</spam>
          </Center>

          <Contact to={"mailto:hesslorlucian85@gmail.com"}>
            <motion.h2
            initial={{
              y:-200,
              transition: { type: "spring", duration: 1.5, delay: 1}
            }}
            animate={{
              y:0,
              transition: { type: "spring", duration: 1.5, delay: 1}
            }}
            whileHover={{scale: 1.1}}
            whileTap={{scale: 0.8}}
            > Hi....   </motion.h2>
          </Contact>
          
          <Skills to="/skills" click={click}>
            <motion.h2
            initial={{
              y:-200,
              transition: { type: "spring", duration: 1, delay: 0}
            }}
            animate={{
              y:0,
              transition: { type: "spring", duration: 1, delay: 0.5}
            }}
            whileHover={{scale: 1.1}}
            whileTap={{scale: 0.8}}
            >MY SKILLS </motion.h2>
          </Skills>
          <Work to="/work" click={click}>
              <motion.h2
              whileHover={{scale: 1.1}}
              whileTap={{scale: 0.8}}
              >WORK </motion.h2>
          </Work>
          <BottomBar>
          <Project to="/project" click={click}>
            <motion.h2
            whileHover={{scale: 1.1}}
            whileTap={{scale: 0.8}}
            > PROJECT </motion.h2>
          </Project>
            <About to="/about">
              <motion.h2
              whileHover={{scale: 1.1}}
              whileTap={{scale: 0.8}}
              > ABOUT </motion.h2>
            </About>
          </BottomBar>
        </Container>
        {click ? <Intro click={click} /> : null }
      </MainContainer>
    </div>
  )
}

export default Main
