import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import V from "../assets/Images/r.mp4"

import { LogoComponent, HomeButton, SocialIcons, AnchorComponent, BackgroundTitle, Music } from '../subComponents'
import { Projects } from "../data/ProjectData";
import ProjectCard from './ProjectCard';
import { motion } from 'framer-motion';


const Container = styled(motion.div)`
background-color: ${props => `rgba(${props.theme.bodyRgba},0)`};
width: 100%;
height: auto;
position: relative;
padding-bottom: 5rem;

.video {
  width: 100vw;
  height: 100vh;
  position: fixed;
  object-fit: cover;
  z-index: -1;
}
`

const Center = styled.div`
display: flex;
justify-content: center;
aligne-items: center;
padding-top: 10rem;
`

const Grid = styled.div`
display: grid;
grid-template-columns: repeat(2, minmax(calc(10rem + 15vw), 1fr));
grid-gap: calc(1rem + 2vw);

@media screen and (max-width: 800px) {
 {
  grid-template-columns: repeat(1,1fr);
 }
`

const container = {

  hidden: {opacity:0},
  show: {
    opacity:1,
    transition: {
      staggerChildren:0.5,
      durination: 0.5
    }
  }
}

const ProjectPage = () => {

  const [numbers,setNumbers] = useState(0);

  useEffect(() => {
    let num = (window.innerHeight - 70)/30;
    setNumbers(parseInt(num));
  },[])

  return (
      <Container
      variants={container}
      initial="hidden"
      animate="show"
      >
      <video className="video" src={V} autoPlay loop muted />
        <LogoComponent />
        <HomeButton />
        <SocialIcons />
        <AnchorComponent numbers={numbers}/>
        <Music />

        <Center>
          <Grid>
            {
             Projects.map((project) => {
              return <ProjectCard key={project.id} project={project} />})
            }
          </Grid>
        </Center>
        <BackgroundTitle text="PROJECT" top="4rem" left="5rem"/>
      </Container>
  )
}

export default ProjectPage
