import React from 'react';
import { Link } from 'react-router-dom';
import { Github,Linkedin, Instagram, Facebook } from '../components/AllSVGs';
import styled from 'styled-components';
import { DarkTheme } from '../components/Themes';
import { motion } from 'framer-motion';

const Icons = styled.div`
display: flex;
flex-direction: column;
align-items: center;

position: fixed;
bottom:0;
left: 2rem;
z-index: 3;
>*:not(:last-child){
    margin: 0.5rem 0;
}
`
const Line = styled(motion.span)`
width: 2px;
height: 8rem;
background-color: ${props => props.color === "dark" ? DarkTheme.text : DarkTheme.body };
`

const SocialIcons = ({ theme }) => {
  return (
    <Icons>
      <motion.div
      initial={{sca1e:(0)}}
      animate={{scale:[0,1,1.5,1]}}
      transition={{type: "spring", duration: 1, delay: 1}}
      >
        <Link to={"https://github.com/Hesslor"} target="_blank" style={{color: "inherit"}}>
            <Github width={25} height={25} fill={theme ==="dark" ? DarkTheme.text : DarkTheme.body}/>
        </Link>
      </motion.div>
      <motion.div
      initial={{sca1e:(0)}}
      animate={{scale:[0,1,1.5,1]}}
      transition={{type: "spring", duration: 1, delay: 1.2}}
      >
        <Link to={"https://www.linkedin.com/in/hesslor-lucian-646343226/"} target="_blank" style={{color: "inherit"}}>
            <Linkedin width={25} height={25} fill={theme ==="dark" ? DarkTheme.text : DarkTheme.body}/>
        </Link>
      </motion.div>
      <motion.div
      initial={{sca1e:(0)}}
      animate={{scale:[0,1,1.5,1]}}
      transition={{type: "spring", duration: 1, delay: 1.4}}
      >
        <Link to={"https://www.instagram.com/hesslor/"} target="_blank" style={{color: "inherit"}}>
            <Instagram width={25} height={25} fill={theme ==="dark" ? DarkTheme.text : DarkTheme.body}/>
        </Link>
      </motion.div>
      <motion.div
      initial={{sca1e:(0)}}
      animate={{scale:[0,1,1.5,1]}}
      transition={{type: "spring", duration: 1, delay: 1.6}}
      >
        <Link to={"https://www.facebook.com/hesslor/"} target="_blank" style={{color: "inherit"}}>
            <Facebook width={25} height={25} fill={theme ==="dark" ? DarkTheme.text : DarkTheme.body}/>
        </Link>
      </motion.div>

      <Line color={theme}
      initial={{
          height: 0
        }}
      animate={{
          height: "8rem"
        }}
      transition={{
          type: "spring", duration: 1, delay: 0.8
        }}
      />
    </Icons>
  )
}

export default SocialIcons
