import React from 'react'
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import styled from 'styled-components'


const Container = styled(motion.div)``;

const Box = styled(motion(Link))`
width: calc(10rem + 15vw);
height: 18rem;
padding: 1rem;
color: ${props => props.theme.body};
text-decoration: none;
border: 2px solid ${props => props.theme.text};
border-radius: 10px;
backdrop-filter: blur(10px);
box-shadow: 0 0rem 1rem 0 rgba(0,0,0,0.5);
cursor: pointer;

display: flex;
flex-direction: column;
z-index: 5;

:hover{
  box-shadow: 0 0rem 1rem 0.2rem rgba(0,0,0,0.8);
  background-color: ${props => `rgba(${props.theme.textRgba},0.4)`};
}
`

const Image = styled.div`
background-image: ${props => `url(${props.img})`};
width: 100%;
height: 50%;
background-size: cover;
border: 1px solid transparent;
background-position: center;

`

const Title = styled.h3`
color: inhiret;
padding: 1rem 0 0.5rem;
font-family: "karla",sans-serif;
font-weight: 800;
border-bottom: 1px solid ${props => props.theme.body};
`

const HashTags = styled.span`
padding: 0.5rem, o;
`
const Tag = styled.span`
padding-right: 0.5rem;
`
const Date = styled.span`
padding: 0.5rem, o;
`

const Item = {
  hidden:{
    scale:0
  },
  show: {
    scale:1,
    transition: {
    type: "spring",
    duration: 0.5
    }
  }
}

const ProjectCard = (props) => {
  const { name, tags, date, imgSrc, link } = props.project;
  return (
    <Container 
    variants={Item}
    >
      <Box target="_blank" to={link}>
        <Image img={imgSrc} alt="okod"/>
        <Title>{name}</Title>
        <HashTags>
          {
            tags.map((tag,id) => <Tag key={id}>#{tag}</Tag>)
          }
        </HashTags>
        <Date>{date}</Date>
      </Box>
    </Container>
  )
}

export default ProjectCard
