import React from 'react'
import styled from 'styled-components'
import { Homebtn } from '../components/AllSVGs'
import { Link } from 'react-router-dom'


const Home = styled.button`
position: fixed;
top: 2rem;
left: 50%;
transform: transalate(-50%,0)
background-color: transparent;
padding: 0.3rem;
border-radius: 50%;
border: 2px solid #ffff;
width: 2.5rem;
height: 2.5rem;
z-index: 1;

display: flex;
justify-content: center;
align-item: center;
cursor: pointer;
:hover{ background-color: #FFDB05; box-shadow: 0 0 10px 4px #FFEA05 }
>*:first-child{ color: inherit; }
`

const HomeButton = () => {
  return (
    <Home>
      <Link to="/">
      <Homebtn fill={"#000"}/>
      </Link>
    </Home>
  )
}

export default HomeButton
