import React, { useRef, useState } from 'react'
import music from "../assets/audio/m.mp3"
import styled from 'styled-components';
import { Sound } from '../components/AllSVGs';

const Box = styled.div`
display: flex;
position: fixed;
top: 3rem;
left: 10rem;
cursor: pointer;
z-index: 10;
color: black
`

const Music = () => {
    
  const ref = useRef(null);
  const [click,setClick] = useState(false);

  const handleClick = () => {
    setClick(!click);

    if(!click){
        ref.current.play();
    }else{
        ref.current.pause();
    }
  }

  return (
    <Box onClick={() => handleClick()}>
      <Sound width="20px" height="20"/>
      <audio src={music} ref={ref} loop/>
    </Box>
  )
}

export default Music
