import React from 'react'
import styled from 'styled-components'
import { motion } from 'framer-motion'

const Box = styled(motion.li)`
width: 16rem;
height: 40vh;
background-color: ${props => `rgba(${props.theme.textRgba},0.6)`};
color: ${props => props.theme.body};
padding: 1.5rem 2rem;
margin-right: 8rem;
border-radius: 50px 0 50px 0;
backdrop-filter: blur(10px);

display: flex;
flex-direction: column;
justify-content: space-between;
border: 4px solid ${props => props.theme.body};
transition: all 0.2 ease;

&:hover{
  background-color: ${props => `rgba(${props.theme.bodyRgba},0.5)`};
  color: ${props => props.theme.text};
  border: 1px solid ${props => props.theme.text};
}
`

const Title = styled.div`
font-size: calc(1em + 0.5vw);
`

const Description = styled.div`
font-size: calc(0.8em + 0.3vw);
font-family: "karla", sans-serif;
font-weight: 500;
`

const Tags = styled.div`
border-top: 2px solid ${props => props.theme.body};
padding-top: 0.5rem;
display: flex;
flex-wrap: wrap;
${Box}:hover &{
  border-top: 2px solid ${props => props.theme.text};
}
`

const Tag = styled.span`
margin-right: 1rem;
font-size: calc(0.8em + 0.3vw);
`

const Footer = styled.div`
display: flex;
justify-content: space-between;
`

const Link = styled.div`
background-color: ${props => props.theme.body};
color: ${props => props.theme.text};
text-decoration: none;
padding: 0.5rem calc(2rem + 2vw);
border-radius: 0 0 0 50px;
font-size: calc(1em + 0.5vw);

${Box}:hover &{
  background-color: ${props => props.theme.text};
  color: ${props => props.theme.body};
}
`

const Item = {
  hidden:{
    scale:0
  },
  show: {
    scale:1,
    transition: {
    type: "spring",
    duration: 0.5
    }
  }
}

const WorkCard = ({data}) => {
    const { id, name, description, tags } = data
  return (
    <Box key={id} variants={Item}>
      <Title>{name}</Title>
      <Description>{description}</Description>
      <Tags>
        {
          tags.map((tag,id) => <Tag key={id}>#{tag}</Tag>)
        }
      </Tags>
      <Footer>
      <Link>Available</Link>
      </Footer>
    </Box>
  )
}

export default WorkCard
