import React, { useEffect, useRef } from 'react'
import styled, { ThemeProvider } from 'styled-components'
import { motion } from 'framer-motion'
import { DarkTheme } from './Themes'
import { LogoComponent, HomeButton, SocialIcons, BackgroundTitle, Music } from "../subComponents"
import WorkCard from './WorkCard'
import { Work } from "../data/WorkData"
import { Hlogo } from './AllSVGs'

const Box = styled.div`
background: linear-gradient(90deg, #001C56 0%, #05E2FF 100%);
color: #000;
height: 400vh;
position: relative;

display: flex;
align-items: center;
`

const Main = styled(motion.ul)`
position: fixed;
top: 12rem;
left: calc(3rem + 10vw);
height: 40vh;
color: #ffff;

display: flex;
`

const Rotate = styled.span`
display: block;
position: fixed;
right: 1rem;
bottom: 1rem;
width: 80px;
height: 80px;
z-index: 1;
`

const container = {

  hidden: {opacity:0},
  show: {
    opacity:1,
    transition: {
      staggerChildren:0.5,
      durination: 0.5
    }
  }
}

const WorkPage = () => {

  const ref = useRef(null);
  const logo = useRef(null)

  useEffect(() => {
    let element = ref.current;

    const rotate = () => {
      element.style.transform = `translateX(${-window.pageYOffset}px)`

      logo.current.style.transform = `rotate(${window.pageYOffset}deg)`
    }

    window.addEventListener("scroll",rotate)
      return () => window.removeEventListener("scroll",rotate)


  },[])

  return (
    <ThemeProvider theme={DarkTheme} >
    <Box>
      <LogoComponent theme={"dark"} />
      <SocialIcons theme={"dark"} />
      <HomeButton />
      <Music />
      
      <Main ref={ref} variants={container} initial="hidden" animate="show">
        {
          Work.map( x =>  <WorkCard key={x.id} data={x} /> )
        }
      </Main>
      <Rotate ref={logo} >
        <Hlogo width={80} height={80} fill={DarkTheme.body} />
      </Rotate>
      <BackgroundTitle text="work" top="10%" right="20%" />
    </Box>
    </ThemeProvider>
  )
}

export default WorkPage

