import React from 'react'
import styled from 'styled-components'
import mypic from "../assets/Images/Himage.png";
import { motion } from 'framer-motion';


const Box = styled(motion.div)`
position: absolute;
left: 50%;
top: 50%;
transform: translate(-50%,-50%);
Width: 60vw;
height: 50vh;
display: flex;

background: linear-gradient(to right,
    ${props => props.theme.body} 50%,
    ${props => props.theme.text} 50%) bottom,
    linear-gradient(to right,
    ${props => props.theme.body} 50%,
    ${props => props.theme.text} 50%) top;
    background-repeat: no-repeat;
    background-size: 100% 2px;
    border-left: 2px solid ${props => props.theme.body};
    border-right: 2px solid ${props => props.theme.text};
z-index: 1;

@media screen and (max-width: 800px) {
  background-color: rgba(0,0,0,0.8); 
}
`

const SubBox = styled.div`
width: 50%;
position: relative;
display: flex;

.pic{
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%,0%);
  width: 100%;
  height: auto;
}
`

const Text = styled.div`
font-size: calc(0.8em + 1.1vw);
color: ${props => props.theme.body};
padding: 2rem;
curser: pointer;

display: flex;
flex-direction: column;
justify-content: space-evenly;

*:last-child{
  color: ${props => `rgba( ${props.theme.bodyRgba},0.6)`};
  font-size: calc(0.5rem + 1vw);
  font-weight: 300;
}
`

const Intro = () => {
  return (
    <Box
    initial={{height:0}}
    animate={{height:"50vh"}}
    transition={{ type: "spring", duration:2, delay:1 }}
    >
      <SubBox>
        <Text>
          <h1>Hesslor Lucian</h1>
          <h4>Programmer,</h4>
          <h6>I'm a passionate developer with a knack for turning ideas into digital reality.</h6>
        </Text>
      </SubBox>
      <SubBox>
        <motion.div
        initial={{opacity: 0}}
        animate={{opacity: 1}}
        transition={{ duration:0.3, delay:2 }}
        >
        <img className="pic" src={mypic} alt="Profile" />
        </motion.div>
      </SubBox>
    </Box>
  )
}

export default Intro
