import React from 'react'
import styled, { ThemeProvider, keyframes } from 'styled-components'
import { DarkTheme } from './Themes'
import { LogoComponent, HomeButton, SocialIcons, ParticleComponent, BackgroundTitle, Music } from "../subComponents"
import astronaut from "../assets/Images/Astronaut.png"

const Box = styled.div`
background-color: ${props => props.theme.body};
width: 100vw;
height: 100vh;
position: relative;
overflow: hidden;
`
const float = keyframes`
0% { transform: translateY(-10px)}
50% { transform: translateY(15px) translateX(15px) }
100% { transform: translateY(-10px)}
`

const Astronaut = styled.div`
position: absolute;
top: 8%;
right: 5%;
width: 20vw;
animation: ${float} 4s infinite linear;

img{
  width:100%;
  height:auto;
}
`

const Main = styled.div`
border: 2px solid ${props => props.theme.text};
color: ${props => props.theme.text};
padding: 2rem;
width: 50vw;
height: 60vh;
line-height: 1.5;
z-index: 3;

display: flex;
justify-content; center;
align-items: center;
font-size: calc(0.6rem + 1vw);
backdrop-filter: blur(4px);

position: absolute;
left: calc(5rem + 5vw);
top: 10rem;
font-family: "Ubutntu Mono" ,monospace;
font-style: italic;
`

const AboutPage = () => {
  return (
    <ThemeProvider theme={DarkTheme} >
    <Box>
      <LogoComponent theme={"dark"} />
      <SocialIcons theme={"dark"} />
      <HomeButton />
      <ParticleComponent theme={"Dark"}/>
      <Music />


      <Astronaut>
        <img src={astronaut} alt='astronaut' />
      </Astronaut>
      <Main>
      I'm a passionate developer with a knack for turning ideas into digital reality.With a strong foundation in coding and a creative eye for design,
      <br /> <br/>
      I thrive on crafting software solutions that not only meet but exceed expectations. Whether it's web development, mobile apps, or any coding challenge, I'm always eager to take on the next project and bring innovation to life.
      <br /> <br/>
      If you like to work with me connect me through my socialmedia mentioned on the left bottom.
      </Main>
    </Box>
    <BackgroundTitle text="ABOUT" top="10%" left="5%"/>
    </ThemeProvider>
  )
}

export default AboutPage

