import React from 'react'
import styled, { ThemeProvider } from 'styled-components'
import { LightTheme } from './Themes'
import { Design, Develope } from "./AllSVGs"
import { LogoComponent, HomeButton, SocialIcons, ParticleComponent, BackgroundTitle, Music } from "../subComponents"

const Center = styled.div`
display: flex;
justify-content: center;
aligne-items: center;
padding-top: 6rem;
`

const Box = styled.div`
background-color: ${props => props.theme.body};
width: 100vw;
height: auto;
position: relative;

display: flex;
flex-direction: row;
justify-content: space-evenly;
align-items: center;

@media screen and (max-width: 750px) {
  flex-direction: column;
}
`

const Main = styled.div`
border: 2px solid ${props => props.theme.text};
color: ${props => props.theme.text};
background-color: ${props => props.theme.body};
font-family: "Ubuntu Monu", monospace;
padding: 2rem;
width: 30vw;
height: 70vh;
z-index: 3;
line-height: 1.5;
cursor: pointer;

display: flex;
flex-direction: column;
justify-content: space-between;

:hover{
  color: ${props => props.theme.body};
  background-color: ${props => props.theme.text};
}

@media screen and (max-width: 750px){
  width: 50vw;
  padding: 2rem;
}
`

const Title = styled.div`
display: flex;
justify-content: center;
align-items: center;
font-size: calc(1rem + 1vw);

&>*:first-child{
  margin-right: 1rem;
}

${Main}:hover &{
  &>*{
    fill: ${props => props.theme.body};
  }
}
`

const Description = styled.div`
color: ${props => props.theme.text};
font-size: calc(0.6rem + 0.5vw);
padding: 0.5rem 0;

strong{
  margin-bottom: 1rem;
  text-transform: uppercase;
}

ul{
  margin-left: 2rem;
}

${Main}:hover &{
    color: ${props => props.theme.body};
  }
`

const MySkillsPage = () => {
  return (
    <ThemeProvider theme={LightTheme} >
    <Center>
      <Box>
        <LogoComponent />
        <SocialIcons />
        <HomeButton />
        <ParticleComponent theme={"light"}/>
        <Music />
        <Main>
          <Title>
            <Design width={40} height={40} /> Designer
          </Title>
          <Description>
          "Passionate Designer | Transforming Ideas into Creative Masterpieces"
          Feel free to customize it further to match your unique style and the specific design work you specialize in.
          </Description>
          <Description>
            <strong>Designing</strong>
            <ul>
              <li>Posters</li>
              <li>Website</li>
              <li>Mobile Application</li>
            </ul>
          </Description>
          <Description>
            <strong>Tools</strong>
            <ul>
              <li>Photoshop</li>
              <li>Figma</li>
              <li>Canva</li>
            </ul>
          </Description>
        </Main>
        <Main>
          <Title>
            <Develope width={40} height={40} /> Developer
          </Title>
          <Description>
          I thrive on crafting software solutions that meet expectations. Whether it's web development, mobile apps, or any coding challenge, I'm always eager to take on the next project and bring innovation to life.
          </Description>
          <Description>
            <strong>Skills</strong>
            <ul>
            <p>Html, CSS, JavaScript, jQuery,
              React, NodeJS, Express, Python, Sql.</p>
            </ul>
          </Description>
          <Description>
            <strong>Tools</strong>
            <ul>
              <li>VS studio</li>
              <li>Codepen</li>
              <li>Github</li>
              <li>Wordpress</li>
            </ul>
          </Description>
        </Main>
        <BackgroundTitle text="SKILLS" top="80%" right="30%"/>
      </Box>
    </Center>
    </ThemeProvider>
  )
}

export default MySkillsPage
